@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
* {
  margin: 0;
}

body {
  display: flex;
  justify-content: center;
}

#root {
  width: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

header {
  max-width: 1440px;
  margin-top: 2%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 90%;
}

header nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 10px;
  width: 100%;
}

header nav img {
  margin-right: 20px;
  height: 40px;
}

header nav ul,
header nav a {
  text-decoration: none;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  font-family: "Montserrat";
  font-style: normal;
  color: #ff6060;
  font-weight: 500;
  font-size: 12px;
  line-height: 142.6%;
}

header nav ul li,
header nav a li {
  margin-left: 20px;
}

@media (min-width: 768px) {
  header nav img {
    height: 55px;
  }
  header nav ul,
  header nav a {
    font-size: 24px;
  }
}

main {
  margin: 5%;
  max-width: 1440px;
}

main .heading {
  margin-bottom: 30px;
  border-radius: 25px;
  overflow: hidden;
  height: 220px;
  background-image: url(/src/Assets/Background.png);
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

main .heading p {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 48px;
  line-height: 142.6%;
  /* or 68px */
  color: #ffffff;
}

main .accommodation-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  background-color: #f6f6f6;
  border-radius: 25px;
  padding-top: 40px;
}

main .accommodation-container .acc-card {
  box-sizing: border-box;
  overflow: hidden;
  margin: 20px;
  min-width: 300px;
  width: calc(28%);
  height: 340px;
  border-radius: 10px;
  position: relative;
}

main .accommodation-container .acc-card::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  z-index: 0;
}

main .accommodation-container .acc-card .acc-cover {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

main .accommodation-container .acc-card .acc-title {
  position: absolute;
  bottom: 10px;
  left: 10px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 142.6%;
  color: #ffffff;
  z-index: 1;
}

@media (max-width: 768px) {
  main .heading {
    height: 110px;
  }
  main .heading p {
    font-size: 34px;
  }
  main .accommodation-container .acc-card {
    width: calc(90%);
  }
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 200px;
  background-color: black;
  color: white;
}

footer p {
  margin: 5px;
}

footer img {
  margin: 5px;
}

.error {
  font-family: "Montserrat";
  font-style: normal;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #ff6060;
  height: 80%;
}

.error h1 {
  font-weight: 700;
  font-size: 290px;
  line-height: 142.6%;
}

.error p {
  font-weight: 500;
  font-size: 36px;
  line-height: 142.6%;
}

.error a {
  font-weight: 500;
  font-size: 18px;
  line-height: 142.6%;
  color: #ff6060;
  margin: 70px;
}

@media (max-width: 768px) {
  .error h1 {
    font-size: 96px;
  }
  .error P {
    font-size: 18px;
  }
}

.about {
  width: 90%;
  margin-top: 2%;
  font-family: "Montserrat";
  font-style: normal;
  max-width: 1440px;
  margin-bottom: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.dropdown button {
  margin-top: 30px;
  width: 100%;
  background-color: #ff6060;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  height: 50px;
  font-weight: 500;
  font-size: 24px;
  line-height: 142.6%;
  border-radius: 5px;
  border: none;
  color: white;
}

.dropdown button:hover {
  cursor: pointer;
}

.dropdown .content {
  padding: 15px;
  background-color: #f6f6f6;
  color: #ff6060;
  border-radius: 5px;
  margin-bottom: 30px;
  transition: all 1s ease-in-out;
}

.dropdown .content ul {
  list-style: none;
  padding: 0;
}

.dropdown .content ul li {
  margin-bottom: 5px;
}

.listing-container {
  font-family: "Montserrat";
  font-style: normal;
  margin: 5%;
  margin-top: 2%;
  color: #ff6060;
  width: 90%;
  max-width: 1440px;
}

.listing-container .listing {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.listing-container .listing .listing-heading {
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.listing-container .listing .listing-heading h1 {
  font-weight: 500;
  font-size: 36px;
  line-height: 142.6%;
}

.listing-container .listing .listing-heading p {
  font-weight: 500;
  font-size: 18px;
  line-height: 142.6%;
}

.listing-container .listing .listing-heading .tags {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 25px 0;
}

.listing-container .listing .listing-heading .tags p {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 142.6%;
  width: 140px;
  background-color: #ff6060;
  color: white;
  padding: 5px;
  margin-right: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
}

.listing-container .listing .listing-info {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  margin: 0;
}

.listing-container .listing .listing-info .host {
  display: flex;
  align-items: center;
  font-size: 18px;
}

.listing-container .listing .listing-info .host img {
  width: 64px;
  border-radius: 50%;
  overflow: hidden;
  margin-left: 20px;
}

.listing-container .drop-container {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: space-between;
  min-height: 300px;
}

.listing-container .drop-container .dropdown {
  margin: 0;
  width: 48%;
}

@media (max-width: 768px) {
  .listing-container .listing .listing-heading .tags {
    width: 100%;
  }
  .listing-container .listing .listing-heading .tags p {
    margin-right: 5px;
    width: 85px;
  }
  .listing-container .listing .listing-info {
    width: 100%;
    flex-direction: row-reverse;
    justify-content: space-between;
  }
  .listing-container .drop-container {
    justify-content: center;
  }
  .listing-container .drop-container .dropdown {
    width: 100%;
  }
}

.gallery {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 420px;
  width: 100%;
  margin-bottom: 30px;
  border-radius: 10px;
  overflow: hidden;
  border-radius: 10px;
}

.gallery .right-arrow {
  cursor: pointer;
  position: absolute;
  top: 45%;
  right: 30px;
}

.gallery .left-arrow {
  cursor: pointer;
  position: absolute;
  top: 45%;
  left: 30px;
}

.gallery .slide {
  opacity: 0;
  transition: 1s ease;
}

.gallery .slide .image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.gallery .active {
  opacity: 1;
  transition: 1s;
  width: 100%;
  height: 100%;
}
